<main>
  {#if !closed}
    <div class="miconsent {showBlockingFlag ? 'blocking' : ''}">
      <div class="miconsent__container">
        {#if viewState === 'HOME'}
          <Home
            {...homeText}
            {highlightColor}
            {sliderColor}
            {imprintLink}
            {privacyLink}
            {language}
            {scripts}
            on:done={handleDone}
            on:close={handleClose}
            on:changeView={changeView} />
        {:else if viewState === 'CHOOSE'}
          <Choose
            {...chooseText}
            {highlightColor}
            {language}
            {sliderColor}
            {scripts}
            on:done={handleDone}
            on:changeView={changeView} />
        {/if}
      </div>
    </div>
  {/if}
</main>
